@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i&display=swap&subset=cyrillic,cyrillic-ext,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
    margin-top: 50px;
    padding: 0px 100px 0px 100px;
}

.logo h5{
    font-family: 'PT Sans', sans-serif;
    color: maroon;
    -webkit-text-decoration: maroon;
            text-decoration: maroon;
    letter-spacing: 7px;
    display: inline;
    padding-left: 15px;
}

.nav:hover{
    background: white;
}

.made{
    color: gray;
    margin-left: 100px;
    margin-top: 50px;
}

.strng {
    border-bottom: 1px dotted #000;
    color: rgb(59, 59, 59);
}

.made a:hover {
    text-decoration: none;
    border-bottom: 1px solid #000;
}
.card {
  position: relative;
  max-width: 100%;
  width: 250px;
  min-width: 250px;
  min-height: 0px;
  display: flex;
  flex-flow: column;
  background-color: #ecf0f1;
  border-radius: 0.25rem;
  margin: 1rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.25s; }
  .card.float:hover {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
    box-shadow: 0 10px 10px -10px #7f8c8d; }

.product-card-description-box {
  height: 0%;
  padding: 1.5rem 2rem;
  background-color: #ecf0f1; }
  .product-card-description-box .row {
    display: flex;
    flex-flow: row;
    margin-top: 0.5rem; }

.product-card-name {
  text-transform: capitalize;
  font-size: 1.5rem;
  letter-spacing: 3px;
  color: #2c3e50; }

.gallery-toggle {
  position: absolute;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 2rem;
  transition: 0.25s;
  color: rgba(236, 240, 241, 0.5);
  background-color: rgba(44, 62, 80, 0.5);
  opacity: 0;
  cursor: pointer; }
  .gallery-toggle:hover {
    opacity: 0; }

.product-card-gallery {
  position: relative;
  display: flex;
  flex-flow: row;
  flex: 0 0 auto;
  height: 50%;
  overflow: hidden; }
  .product-card-gallery .gallery-item {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0.25rem 0.25rem 0 0;
    opacity: 0;
    transition: 1s; }
  .product-card-gallery .active {
    width: 100%;
    opacity: 1; }

.price-tag {
  position: absolute;
  background-color: rgba(44, 62, 80, 0.95);
  color: #ecf0f1;
  border-radius: 0.25rem;
  padding: 0.125rem 0.5rem;
  top: 1rem;
  left: 1rem; }

.product-card {
  width: 200px;
  height: 250px;
  box-shadow: 0 10px 30px rgba(44, 62, 80, 0.5); }

.product{
    margin-top: 20px;
    display: flex;
}

.linkto {
    color: gray;
}

.linkto:hover{
    color: black;
    text-decoration: none;
}

@media screen and (max-width: 575px) {
    .product {
        justify-content: center;
        display: block;
        flex: 0 0 20vw;
    }
  }
.linkto {
    color: gray;
}

.linkto:hover{
    color: black;
    text-decoration: none;
}

